'use client'

import { useEffect } from 'react'
import { useRouter } from 'next/navigation'
import StreakCounter from '@/components/StreakCounter'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'

export default function DashboardPage() {
  const router = useRouter()

  // Check if user is logged in
  useEffect(() => {
    const user = localStorage.getItem('user')
    if (!user) {
      router.push('/login')
    }
  }, [router])

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="max-w-4xl mx-auto space-y-8">
        <div className="text-center">
          <h1 className="text-4xl font-bold text-primary mb-4">
            Dashboard
          </h1>
          <p className="text-xl text-muted-foreground">
            Track your daily streaks and stay consistent with your goals
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <Card>
            <CardHeader>
              <CardTitle>Current Streak</CardTitle>
              <CardDescription>
                Your daily streak progress
              </CardDescription>
            </CardHeader>
            <CardContent>
              <StreakCounter />
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Streak Tips</CardTitle>
              <CardDescription>
                How to maintain your streak
              </CardDescription>
            </CardHeader>
            <CardContent>
              <ul className="space-y-2 text-sm text-muted-foreground">
                <li>• Check in every day at the same time</li>
                <li>• Set reminders on your phone</li>
                <li>• Track your progress visually</li>
                <li>• Don't break the chain!</li>
              </ul>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
