'use client'

import { useState, useEffect, useCallback } from 'react'
import { differenceInCalendarDays, parseISO, format } from 'date-fns'

interface StreakData {
  count: number
  lastCheckIn: string | null
}

export function useStreak() {
  const [streak, setStreak] = useState<number>(0)
  const [lastCheckIn, setLastCheckIn] = useState<string | null>(null)
  const [error, setError] = useState<string | null>(null)

  // Load streak data from localStorage
  useEffect(() => {
    try {
      const stored = localStorage.getItem('streakData')
      if (stored) {
        const data: StreakData = JSON.parse(stored)
        setStreak(data.count)
        setLastCheckIn(data.lastCheckIn)
      }
    } catch (err) {
      setError('Failed to load streak data')
    }
  }, [])

  // Save streak data to localStorage
  const saveStreakData = useCallback((count: number, lastDate: string) => {
    try {
      const data: StreakData = { count, lastCheckIn: lastDate }
      localStorage.setItem('streakData', JSON.stringify(data))
    } catch (err) {
      setError('Failed to save streak data')
    }
  }, [])

  // Check in function to update streak
  const checkIn = useCallback(() => {
    try {
      const today = new Date()
      const todayStr = today.toISOString().split('T')[0]

      if (lastCheckIn) {
        const lastDate = parseISO(lastCheckIn)
        const diffDays = differenceInCalendarDays(today, lastDate)

        if (diffDays === 0) {
          // Already checked in today, do nothing
          return
        } else if (diffDays === 1) {
          // Increment streak
          const newCount = streak + 1
          setStreak(newCount)
          setLastCheckIn(todayStr)
          saveStreakData(newCount, todayStr)
        } else {
          // Missed a day, reset streak
          setStreak(1)
          setLastCheckIn(todayStr)
          saveStreakData(1, todayStr)
        }
      } else {
        // First check-in ever
        setStreak(1)
        setLastCheckIn(todayStr)
        saveStreakData(1, todayStr)
      }
    } catch (err) {
      setError('Failed to update streak')
    }
  }, [lastCheckIn, saveStreakData, streak])

  // Format last check-in date for display
  const formattedLastCheckIn = lastCheckIn ? format(parseISO(lastCheckIn), 'PPP') : 'Never'

  return {
    streak,
    lastCheckIn: formattedLastCheckIn,
    checkIn,
    error,
    clearError: () => setError(null),
  }
}
